import React, { Component }  from 'react';

import { BrowserRouter, Route } from 'react-router-dom'

import DetailPage from './detailPage_react.js'

import './App.css'

class App extends Component{
    render() {
        return (
        	<BrowserRouter>
        	    <div>
        	      <Route path="/reservation/detail/:id" render={(props) => <DetailPage {...props}/> }/>
        	    </div>
        	</BrowserRouter>

        );
    }
}

export default App;
